bas Neue', cursive;\r\n}\r\n\r\n::-webkit-scrollbar {\r\n  width: 10px;\r\n}\r\n\r\n::-webkit-scrollbar-track {\r\n  background: var(--background);\r\n}\r\n\r\n::-webkit-scrollbar-thumb {\r\n  background: var(--secondary);\r\n}\r\n\r\n:root {\r\n  --background: #232122;\r\n  --secondaryBackground: #333132;\r\n  --secondary: #e4324c;\r\n  --button-hover: #c42b42;\r\n  --button-active: #a12437;\r\n  --hoverLight: #e6909d;\r\n}\r\n\r\n::-webkit-scrollbar-thumb:hover {\r\n  background: var(--button-hover);\r\n}\r\n\r\nbody {\r\n  background-color: var(--background);\r\n}\r\n\r\nbutton {\r\n  border: none;\r\n  padding: 5px;\r\n  background-color: var(--secondary);\r\n  transition: all 0.2s ease-out;\r\n}\r\n\r\nbutton:hover {\r\n  background-color: var(--button-hover);\r\n}\r\n\r\nbutton:active {\r\n  background-color: var(--button-active);\r\n}\r\n\r\nheader {\r\n  padding: 1rem;\r\n  display: flex;\r\n  justify-content: space-around;\r\n  align-items: center;\r\n  background-color: var(--secondaryBackground);\r\n}\r\n\r\n.logo {\r\n  text-decoration: none;\r\n  font-size: 3rem;\r\n}\r\n\r\n.like-icon span {\r\n  padding-top: 26px;\r\n  display: block;\r\n  color: #000;\r\n  transition: all 0.2s ease-out;\r\n}\r\n\r\n.like-icon span:hover {\r\n  color: var(--button-hover);\r\n}\r\n\r\n.logo span:nth-child(1) {\r\n  font-family: 'Major Mono Display', monospace;\r\n}\r\n\r\n.logo span:nth-child(2) {\r\n  font-family: 'Major Mono Display', monospace;\r\n  color: var(--button-hover);\r\n}\r\n\r\n.nav-links ul {\r\n  list-style: none;\r\n  display: flex;\r\n}\r\n\r\n.nav-links a {\r\n  padding: 1rem;\r\n  text-decoration: none;\r\n  color: #fff;\r\n  font-size: 1.5rem;\r\n  transition: all 0.2s ease-out;\r\n}\r\n\r\n.copyright a {\r\n  text-decoration: none;\r\n  color: var(--secondary);\r\n  transition: all 0.2s ease-out;\r\n}\r\n\r\n.nav-links a:hover {\r\n  color: var(--button-hover);\r\n}\r\n\r\n.footer-text {\r\n  padding: 2rem;\r\n  display: flex;\r\n  justify-content: center;\r\n  background-color: var(--secondaryBackground);\r\n}\r\n\r\n.copyright {\r\n  padding: 1rem 2rem;\r\n  display: flex;\r\n  justify-content: center;\r\n  border-top: 0;\r\n  background-color: #c1c1c1;\r\n}\r\n\r\n.copyright a:hover {\r\n  text-decoration: underline;\r\n  color: var(--button-hover);\r\n}\r\n\r\n.like-icon {\r\n  text-decoration: none;\r\n  background: url('./assets/heart.svg') no-repeat center top;\r\n  background-size: 20px 20px;\r\n  display: block;\r\n  filter: invert(1);\r\n}\r\n\r\n.like-icon:hover {\r\n  filter: none;\r\n}\r\n\r\n.list {\r\n  padding: 5% 0;\r\n  list-style: none;\r\n  display: flex;\r\n  gap: 20vh 10vw;\r\n  flex-flow: row wrap;\r\n  width: 80%;\r\n  justify-content: center;\r\n  margin: 0 auto;\r\n}\r\n\r\n.list-img {\r\n  width: 300px;\r\n  height: 400px;\r\n}\r\n\r\n.list-item {\r\n  border: 5px inset var(--secondary);\r\n  transition: all 0.2s ease-out;\r\n  box-shadow: 0 0 10px black;\r\n}\r\n\r\n.list-item:hover {\r\n  border: 5px inset var(--hoverLight);\r\n  transform: scale(1.02);\r\n  box-shadow: 0 0 20px black;\r\n}\r\n\r\n.series-title {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  padding: 3px;\r\n}\r\n\r\n.series-title p {\r\n  font-size: 1.3rem;\r\n}\r\n\r\n.buttons {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 3px;\r\n  padding: 3px;\r\n}\r\n\r\n/* Comments Wrapper */\r\n\r\n.comment-wrapper {\r\n  overflow: scroll;\r\n  position: fixed;\r\n  top: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  display: flex;\r\n  backdrop-filter: blur(10px);\r\n  background-color: var(--background);\r\n  opacity: 0.97;\r\n}\r\n\r\n.comment-container {\r\n  margin: auto;\r\n  padding: 1rem;\r\n  position: relative;\r\n  border: 5px inset var(--secondary);\r\n  border-radius: 7px;\r\n  width: 60%;\r\n  max-width: 800px;\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  gap: 1rem;\r\n  box-shadow: 0 0 20px #000;\r\n}\r\n\r\n.cross-icon {\r\n  position: absolute;\r\n  top: 5px;\r\n  right: 5px;\r\n  background: url(./assets/cross-icon.png) no-repeat center center;\r\n  width: 30px;\r\n  background-size: 30px;\r\n  height: 30px;\r\n  cursor: pointer;\r\n  filter: invert(1);\r\n}\r\n\r\n.image {\r\n  width: 80%;\r\n}\r\n\r\n.item-info {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  width: 80%;\r\n  background-color: var(--button-active);\r\n  font-size: 1.3rem;\r\n}\r\n\r\n.comments {\r\n  width: 80%;\r\n  text-align: center;\r\n}\r\n\r\n.comment-box {\r\n  display: flex;\r\n  justify-content: space-around;\r\n  padding: 1.2rem;\r\n  background-color: var(--background);\r\n}\r\n\r\n.comment-box span:nth-child(2) {\r\n  font-family: 'Handlee', cursive;\r\n}\r\n\r\n.comment-box:nth-child(odd) {\r\n  background-color: var(--secondaryBackground);\r\n}\r\n\r\n.add-comment {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 1rem;\r\n  text-align: center;\r\n  width: 80%;\r\n}\r\n\r\n.add-comment form {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 1rem;\r\n}\r\n\r\n.comment-form input,\r\ntextarea {\r\n  resize: none;\r\n  padding: 3px 5px;\r\n  outline: none;\r\n  text-align: center;\r\n  color: #000;\r\n}\r\n"],"sourceRoot":""}]);
